              <div v-if="belbinNajvisiPrvi==0">
                <p><b>Primarna Uloga: Snalazljivi Organizator ({{user.belbinTest.SO}})</b> Koncepte i planove pretvara u prakticnu akciju. Planove ostvaruje sistematicno i efektno. Odlike: uravnotezen i disciplinovan. Zahvaljujuci njemu projekti i planovi se i prakticno primenjuju. Voli tacnost i doslednost, ne voli kada se planovi menjaju.</p>
