              <div v-if="belbinNajvisiPrvi==1">
                <p><b>Primarna Uloga: Rodjeni Vodja ({{user.belbinTest.RV}})</b> Daje svoj pecat i kontrolise nacin na koji grupa pokusava da ostvari svoje ciljeve. Sposoban je da uspesno iskoristi kvalitete koje grupa poseduje. Zna koje su jake, a koje su slabe tacke clanova grupe i u stanju je da iskoristi prednosti i potencijale svakog pojedinca u grupi. Odlike: uravnotezen, dominantan, ekstrovertan. Vise se oslanja na zdravorazumsko rasudjivanje nego na intelektualno promisljanje. Nije agresivan dok upravlja grupom.</p>
