    var doc = new jsPDF("l","pt","a4");
    doc.html(document.querySelector("#kpi"),{
      callback: function(pdf){
        pdf.save("mypdf.pdf");
      }
    });
  },
  makePDFBelvin(){
    window.html2canvas=html2canvas;
