  isMobile() {
    if(/Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent)) {
      return true
    } else {
      return false
    }
  },
  dateExistsVQ(){
    let str =this.user.vqTest.date.substring(0, 19) ;
    this.vqDate= "Datum: "+str.substring(0,4)+"."+str.substring(5,7)+"."+str.substring(8,10)+". Vreme:"+str.substring(11,19);
},
