<template lang="html">



  <div id="evaluation1">
    <div class="header">

    <h2>{{company.name}}</h2>
    <h3>Procena radnog učinka rađenog {{date}}</h3>

    </div>

    <!-- <div class="container"> -->
    <div class="left">

      <table>
        <tr>
          <th>Ime i prezime</th>
          <th>Radno mesto</th>
          <th>Ocena Konačnog učinka</th>
          <th>Kompetence</th>
          <th>Lični</th>
          <th>Kompanijski</th>
        </tr>


        <!-- <tr>
          <div class="singleEval" v-for="(user,i) in firstBatch" v-bind:key="i" >
            <div >
              {{user.name}} : {{evals[i]}}
            </div>
          </div>
        </tr> -->

        <tr v-for="(user,i) in firstBatch" v-bind:key="i" >
          <td>{{user.name}}</td>
          <td>{{user.radnoMesto}}</td>

            <td class="red" v-if="evals[i].kpi<0.75">
              {{evals[i].kpi}}
            </td>
            <td class="yellow" v-if="evals[i].kpi>=0.75 && evals[i].kpi<=0.99">
              {{evals[i].kpi}}
            </td>
            <td class="blue" v-if="evals[i].kpi>0.99 && evals[i].kpi<1.17">
              {{evals[i].kpi}}
            </td>
            <td class="green" v-if="evals[i].kpi>=1.17">
              {{evals[i].kpi}}
            </td>

          <td v-if="evals[i].kompetence!='-'">{{evals[i].kompetence.toFixed(2)}}</td>
          <td v-if="evals[i].kompetence=='-'">{{evals[i].kompetence}}</td>
          <td v-if="evals[i].licni!='-'">{{evals[i].licni.toFixed(2)}}</td>
          <td v-if="evals[i].licni=='-'">{{evals[i].licni}}</td>
          <td v-if="evals[i].kompanijski!='-'">{{evals[i].kompanijski.toFixed(2)}}</td>
          <td v-if="evals[i].kompanijski=='-'">{{evals[i].kompanijski}}</td>

        </tr>

      </table>


    <!-- <div class="singleEval" v-for="(user,i) in firstBatch" v-bind:key="i" >
      <div class="red" v-if="evals[i]<0.75">
        {{user.name}} : {{evals[i]}}
      </div>
      <div class="yellow" v-if="evals[i]>=0.75 && evals[i]<=0.99">
        {{user.name}} : {{evals[i]}}
      </div>
      <div class="blue" v-if="evals[i]>0.99 && evals[i]<=1.17">
        {{user.name}} : {{evals[i]}}
      </div>
      <div class="green" v-if="evals[i]>1.17">
        {{user.name}} : {{evals[i]}}
      </div>
    </div> -->

    </div>

    <div class="right">
      <div class="singleEval" v-for="(user,i) in secondBatch" v-bind:key="i" >
        <div class="red" v-if="evals[i]<0.75">
          {{user.name}} : {{evals[i]}}
        </div>
        <div class="yellow" v-if="evals[i]>=0.75 && evals[i]<=0.99">
          {{user.name}} : {{evals[i]}}
        </div>
        <div class="blue" v-if="evals[i]>0.99 && evals[i]<=1.17">
          {{user.name}} : {{evals[i]}}
        </div>
        <div class="green" v-if="evals[i]>1.17">
          {{user.name}} : {{evals[i]}}
        </div>
      </div>
    </div>

    <!-- </div> -->

  </div>


  <button type="button" name="button" @click="makePDF">pdf</button>
</template>

<script>
// eslint-disable-next-line no-unused-vars
import {mapActions,mapGetters} from 'vuex';
// eslint-disable-next-line no-unused-vars
import html2canvas from 'html2canvas';
// eslint-disable-next-line no-unused-vars
import jsPDF from 'jspdf';

export default {
  props:{
    company:{
      type: Object
    }
  },
  computed: mapGetters(['usersSameCompany']),
  data(){
    return{
      collaguesNumber:0,
      lastEval:0,
      date:"",
      evals:[],
      firstBatch:[],
      secondBatch:[],
    }
  },
  methods:{
    ...mapActions(['getCollegues']),
    async makePDF(){

      var downloading = document.getElementById("evaluation1");
      var doc = new jsPDF("p","pt","a3");

      await html2canvas(downloading,{
        scale: 2,
        allowTaint : true,
        useCORS: true,
        height:1190,
        width:840,
      }).then((canvas)=>{
        doc.addImage(canvas.toDataURL("image/png"),'PNG',0,0,840, 1190);
      });

      doc.save("Document.pdf");

    },
    compare(a,b) {
      if (a.kpi > b.kpi)
        return -1;
      if (a.kpi < b.kpi)
        return 1;
        return 0;
    },
  },
  async created(){
    await this.getCollegues(this.company.name);
    this.lastEval=0;
    for (var i = 0; i < this.usersSameCompany.length; i++) {
      let tempUser = this.usersSameCompany[i];
      if(tempUser.performanceEvaluation.length>0){
        if((tempUser.performanceEvaluation.length-1 )>=this.lastEval ){
          this.lastEval=tempUser.performanceEvaluation.length-1 ;
          if(tempUser.performanceEvaluation[this.lastEval].date!=""){
          this.date =tempUser.performanceEvaluation[this.lastEval].date;
        }
        }
      }
    }



    let tempIndex=3;

    if(this.date[1]=='.'){
      tempIndex=2;
    }
    if(this.date[tempIndex]=='1'){
      this.date="Januara 2022."
    }
    if(this.date[tempIndex]=='2'){
      this.date="Februara 2022."
    }
    if(this.date[tempIndex]=='3'){
      this.date="Marta 2022."
    }
    if(this.date[tempIndex]=='4'){
      this.date="Aprila 2022."
    }
    if(this.date[tempIndex]=='5'){
      this.date="Maja 2022."
    }
    if(this.date[tempIndex]=='6'){
      this.date="Juna 2022."
    }
    if(this.date[tempIndex]=='7'){
      this.date="Jula 2022."
    }
    if(this.date[tempIndex]=='8'){
      this.date="Avgusta 2022."
    }
    if(this.date[tempIndex]=='9'){
      this.date="Septembra 2022."
    }
    if(this.date[tempIndex]=='10'){
      this.date="Oktobra 2022."
    }
    if(this.date[tempIndex]=='11'){
      this.date="Novembra 2022."
    }
    if(this.date[tempIndex]=='12'){
      this.date="Decembra 2022."
    }




    for ( i = 0; i < this.usersSameCompany.length; i++) {




      let tempUser = this.usersSameCompany[i];


      let ukupnoKompetence = 0;

      if(this.company.performanceEvaluation.kompetenceProcenat>0){

      if(tempUser.performanceEvaluation.length>0){
        for ( var j = 0; j < tempUser.performanceEvaluation[tempUser.performanceEvaluation.length-1].kompetence.length; j++) {
          if(tempUser.performanceEvaluation[tempUser.performanceEvaluation.length-1].kompetence[j].konacnaProcena!=null){
            ukupnoKompetence+=tempUser.performanceEvaluation[tempUser.performanceEvaluation.length-1].kompetence[j].rangiranje;
          }
        }
      }
      }



      // this.kompetenceOcena= ukupnoKompetence;

      //licni
      let ukupnoLicni=0;
      if(this.company.performanceEvaluation.licniProcenat>0){
      if(tempUser.performanceEvaluation.length>0){

        for ( j = 0; j < tempUser.performanceEvaluation[tempUser.performanceEvaluation.length-1].licniKpi.length; j++) {
          if(tempUser.performanceEvaluation[tempUser.performanceEvaluation.length-1].licniKpi[j].konacnaProcena!=null){
            ukupnoLicni+=tempUser.performanceEvaluation[tempUser.performanceEvaluation.length-1].licniKpi[j].rangiranje;
          }
        }
      }
      }
      console.log("i = "+i);
      // this.licniOcena=ukupnoLicni;
      //kompanijski
      let ukupnoKompanijski=0;

      if(this.company.performanceEvaluation.kompanijskiProcenat>0){
      if(tempUser.performanceEvaluation.length>0){

      for ( j = 0; j < tempUser.performanceEvaluation[tempUser.performanceEvaluation.length-1].kompanijski.length;j++) {
        if(tempUser.performanceEvaluation[tempUser.performanceEvaluation.length-1].kompanijski[j].konacnaProcena!=null){
          ukupnoKompanijski+= tempUser.performanceEvaluation[tempUser.performanceEvaluation.length-1].kompanijski[j].rangiranje;
        }
      }
      }
      }

      // this.kompanijskiOcena=ukupnoKompanijski;
      let ukupnoKpi=0;
       ukupnoKpi = this.company.performanceEvaluation.kompetenceProcenat/100 * ukupnoKompetence + this.company.performanceEvaluation.licniProcenat/100 * ukupnoLicni + this.company.performanceEvaluation.kompanijskiProcenat/100*ukupnoKompanijski;

      ukupnoKpi=Math.round(ukupnoKpi * 100) / 100;

      let element={
        kpi:ukupnoKpi,
        id:tempUser._id,
        kompetence:ukupnoKompetence,
        licni:ukupnoLicni,
        kompanijski:ukupnoKompanijski,
      }

      if(this.company.performanceEvaluation.kompetenceProcenat==0) element.kompetence='-';
      else Math.round(element.kompetence   * 100) / 100
      if(this.company.performanceEvaluation.licniProcenat==0) element.licni='-';
      else Math.round(element.licni  * 100) / 100
      if(this.company.performanceEvaluation.kompanijskiProcenat==0) element.kompanijski='-';
      else Math.round(element.kompanijski * 100) / 100
      this.evals.push(element);



    }



    let smaller;
    if(38 < this.usersSameCompany.length){
      smaller = 38;
    }else{
        smaller = this.usersSameCompany.length;
    }

    let tempBatch=[];
    for ( i = 0; i < smaller ; i++) {
      tempBatch.push(this.usersSameCompany[i]);
    }
    for ( i = 38; i < this.usersSameCompany.length; i++) {
      this.secondBatch.push(this.usersSameCompany[i]);
    }

    this.evals.sort(this.compare);

    for ( i = 0; i < smaller; i++) {
      let tmpUsr;
      for ( j = 0; j < tempBatch.length; j++) {
        if(this.evals[i].id == tempBatch[j]._id){
          tmpUsr=tempBatch[j];
        }
      }
      this.firstBatch.push(tmpUsr);
    }

    for ( i = 0; i < this.evals.length; i++) {
      if (!this.evals[i].kpi){
        this.evals.splice(i,1);
        this.firstBatch.splice(i,1);
        i--;
      }
      else if(this.evals[i].kpi==0 ){

        this.evals.splice(i,1);
        this.firstBatch.splice(i,1);
        i--;
      }
    }
  }
}
</script>

<style lang="css" scoped>
#evaluation1{
  border-style: solid;
  border-color: black;
  border-width: thin;
  padding: 30px;

  height:1190px;
  width:840px;
}
.red{

  background-color: #FFC2C2;

}
.yellow{

  background-color: #FFFFC2;

}
.blue{

  background-color: #AACCFF;

}
.green{

  background-color: #C2FFC2;

}
.container {
  display: grid;
  grid-auto-columns: 1fr;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr;
  gap: 0px 0px;
  grid-template-areas:
    ". .";
}
.header{
  border-style: solid;
  border-color: black;
  border-width: thin;
  padding: 30px;

  text-align: center;
  margin-bottom: 10px;

}
.left{
  margin-right: 10px;
}

table{
  border: 2px solid black;
  text-align: center;
  margin:auto;
}
td, th{
  border: 1px solid black;
  text-align: center;
}
th{
  padding: 2px;
}
</style>
